<template>
  <div>
    <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mb-3 border-bottom">
      <h1 class="h2">Locao <small class="operation">Alterar</small></h1>
      <div class="btn-toolbar mb-2 mb-md-0">
        <button @click="editOperation = !editOperation" class="btn btn-sm btn-outline-secondary mr-2">
          <span data-feather="edit-3"></span>
          Editar
        </button>
        <router-link :to="{ name: 'back.returns.create' }" class="btn btn-sm btn-outline-secondary mr-2">
          <span data-feather="check-circle"></span>
          Devolver
        </router-link>
        <button @click="handleDestroy" class="btn btn-sm btn-outline-secondary">
          <i data-feather="trash-2"></i>
          Cancelar
        </button>
      </div>
    </div>
    <div>
      <form>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="item">Item</label>
            <select v-model="itemSelected" @change="calculate" class="form-control" id="item" name="item" :disabled="!editOperation" >
              <option value="">Selecione...</option>
              <option v-for="item in items" :value="item.id">{{ item.name }}</option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="customer">Cliente</label>
            <select class="form-control" id="customer" name="customer" :disabled="!editOperation">
              <option>Selecione...</option>
              <option v-for="customer in customers" :value="customer.id">{{ customer.name }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-3">
            <label for="dt_devolucao">Dt. Devoluo Prevista</label>
            <input-date id="dt_devolucao" :defaultDate="expectedDateDevolution" :disabled="!editOperation"/>
          </div>
          <div class="form-group col-md-3">
            <label for="value">Valor</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text">R$</span>
              </div>
              <input type="text" class="form-control" id="value" placeholder="0,00" v-model="value" :disabled="!editOperation">
            </div>
          </div>
        </div>
        <div class="text-right">
          <button :disabled="!editOperation" type="submit" class="btn btn-primary">Atualizar</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  import InputDate from '@/components/Form/InputDate'
  export default {
    name: 'Location',
    components: { InputDate },
    data () {
      return {
        itemSelected: '',
        items: [
          {id: 1, name: 'Poeira em Alto Mar', value: 2.99, deadline: new Date(2018, 2, 15)},
          {id: 2, name: 'Homem das Cavernas', value: 1.99, deadline: new Date(2018, 4, 10)}
        ],
        customers: [
          {id: 1, name: 'Edilson Cichon'},
          {id: 2, name: 'Fernanda Rodrigues'}
        ],
        expectedDateDevolution: new Date(),
        value: '',
        editOperation: false
      }
    },
    methods: {
      getItemSelected (id) {
        return this.items.find(function (item) {
          return item.id === id
        })
      },
      calculate () {
        let item = this.getItemSelected(this.itemSelected)
        if (item) {
          this.value = item.value
          this.expectedDateDevolution = item.deadline
        } else {
          this.value = ''
        }
      },
      handleDestroy () {
        let confirmation = confirm('Tem certeza que deseja cancelar esta Locao?')
        if (confirmation) {
          alert('Locao cancelada com sucesso!')
        }
      }
    },
    created () {
      this.$bus.$emit('activateMenuItem', { menu: 'locations' })
    }
  }
</script>



